.form-field {
  display: block;
  width: 100%;
  height: 48px;
  border: 1px solid transparent;
  border-radius: 8px;
  padding: 14px 16px;
  color: $color-dark;
  background-color: #f7f8ff;

  transition: border-color 0.3s;
  
  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #8f99a8;
  }
  
  &:hover {
    border-color: #ccd0e0;
  }
  
  &:focus {
    border-color: $color-blue;
    outline: none;
  }

  &--textarea {
    height: 144px;
    resize: none;
    font-family: inherit;
  }

}
